<template>
  <div class="container">
    <ul class="nav nav-tabs">
      <li class="nav-item">
        <a class="nav-link active" data-toggle="tab" href="#menu1">Order report</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu2">Income report</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu3">In/out report</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu4">Export/inport</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" data-toggle="tab" href="#menu5">Summary</a>
      </li>
    </ul>

    <!-- Tab panes -->
    <div class="tab-content mt-1">
      <div id="menu1" class="container tab-pane active">
        <h4>Order count: {{order.length}}</h4>
        <h5>Completed order: {{filterData(order, 'is_done', true).length}}</h5>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in filterData(order, 'is_done', true)" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.is_done ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
        <h5>Pending: {{filterData(order, 'is_done', false).length}}</h5>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in filterData(order, 'is_done', false)" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.is_done ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div id="menu2" class="container tab-pane">
        <h4>Completed bill count: {{filterData(bill, 'is_done', true).length}}</h4>
        <h5>Total: {{sum(bill, 'is_done', true)}}</h5>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Total</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in filterData(bill, 'is_done', true)" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.total}}</td>
              <td>{{item.is_done ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
        <h4>Incompleted bill count: {{filterData(bill, 'is_done', false).length}}</h4>
        <h5>Total: {{sum(bill, 'is_done', false)}} </h5>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Total</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in filterData(bill, 'is_done', false)" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.total}}</td>
              <td>{{item.is_done ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
        <!-- {{bill}} -->
      </div>
      <div id="menu3" class="container tab-pane">
        {{pay}}
        <h4>Pay: {{pay.length}}</h4>
      </div>
      <div id="menu4" class="container tab-pane">
        <!-- {{exportTicket}} -->
        <h4>Export</h4>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in exportTicket" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.is_confirmed ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
        <h4>Import</h4>
        <table class="table">
          <thead class="thead-dark">
            <th>#</th>
            <th>ID</th>
            <th>Name</th>
            <th>Date</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in importTicket" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.id}}</td>
              <td>{{item.name}}</td>
              <td>{{new Date(item.created_time).toLocaleDateString()}}</td>
              <td>{{item.is_confirmed ? 'Done' : 'Pending'}}</td>
            </tr>
          </tbody>
        </table>
        <!-- {{importTicket}} -->
      </div>
      <div id="menu5" class="container tab-pane">
        <!-- {{item}} -->
        <div class="form-group">
          <label class="control-label" for=""></label>
          <input class="form-control" type="date" v-model="selectedDate" @change="fetchMoney()">
        </div>
        <h5>Điểm hòa vốn tháng {{selectedMonth}}: {{new Date(money.time).toUTCString()}}</h5>
        <h5>Quỹ tiền hiện có: {{money.money}}</h5>
      </div>
    </div>
  </div>
</template>
<script>
import u from "../modules/util";
import db from "../configs/firebase.config";
  
export default {
  name: 'report',
  data() {
    return {
      item: '',
      order: '',
      pay: '',
      exportTicket: '',
      importTicket: '',
      bill:'',
      incomeTotal: 0,
      selectedDate: new Date().toISOString().substr(0, 10),
      money: '' 
    }
  },
  computed: {
    selectedMonth() {
      return  new Date(this.selectedDate).getMonth() + 1 + '/' + new Date(this.selectedDate).getFullYear()
    }
  },
  methods: {
    filterData(a, property, value) {
      if (a) {
        let t = a.filter(b => b[property] == value) || []
        return t
      }
    },
    sum(b, property, value) {
      let a = this.filterData(b, property, value)
      return a.reduce((acc, cur) => acc + +cur.total, 0).toFixed(5);
    },
    fetchMoney() {
      let d = new Date(this.selectedDate).getMonth() + '_' + new Date(this.selectedDate).getFullYear()
      u.setCollection('money').get(d, doc => {
        this.money = doc
      })
    }
  },
  created() {
    u.setFirebaseDB(db)
    u.setCollection('orders').setOrderBy('created_time', 'desc').getAllRealTime(doc => this.order = doc)
    u.setCollection('bills').setOrderBy('created_time', 'desc').getAllRealTime(doc => this.bill = doc)
    u.setCollection('ImportBill').setOrderBy('created_time').getAllRealTime(doc => this.pay = doc)
    u.setCollection('tickets').setOrderBy('created_time').getAllRealTime(doc => this.exportTicket = doc)
    u.setCollection('ImportTicket').setOrderBy('created_time').getAllRealTime(doc => this.importTicket = doc)
    u.setCollection('items').setOrderBy('created_time').getAllRealTime(doc => this.item = doc)
    let d = new Date(this.selectedDate).getMonth() + '_' + new Date(this.selectedDate).getFullYear()
      u.setCollection('money').get(d, doc => {
        this.money = doc
      })
  }
}
</script>
<style>
  
</style>


